body {
    background-color: #000; }
.navbar {
    background-color: #000; }
.navbar-brand {
    img {
        object-fit: contain;
        width: 20%; } }

.navbar-toggler {
    background-color: #fff; }
.brandMg {
    padding-left: 5%; }

.navbar-brand {
    cursor: default; }
.carousel-item {

    span {
        color: #fff; }
    img {
        object-fit: cover;
        width: 1600px;
        height: 720px; } }
.carousel {
    max-width: 100%; }

.black {
    opacity: 0.4;
    width: 10000000px;
    height: 720px;
    z-index: 1;
    position: absolute;
    background-color: #000; }
.carousel-caption {
    position: absolute;
    z-index: 3;
    h5 {
        color: #ffffff; }
    p {
        color: #ffffff; } }

.container {
    max-width: 1920px;
    margin: 0 auto; }
.news {
    display: flex;
    justify-content: center;
    div {
        margin: 2.5% 2.5%; }

    span {
        color: #fff;
        text-align: center;
        margin-bottom: 5%;
        font-size: 60px; } }
.card {
    padding-top: 20px;
    margin-bottom: 5%;
    img {
        height: 240px; } }

@media screen and (max-width: 450px) {
    .navbar-brand {
        width: 200px; }
    .news {
        span {
            font-size: 40px; } } }

@media screen and (max-width: 290px) {
    .navbar-brand {
        width: 180px;
        img {
            width: 80px; } }
    .news {
        span {
            font-size: 35px; } } }

@media screen and (max-width: 663px) {
    .news {
        justify-content: center; } }
.carousel-control-prev-icon {
    background-image: url('https://cdn-icons-png.flaticon.com/512/93/93634.png'); }
.carousel-control-next-icon {
    background-image: url('https://cdn-icons-png.flaticon.com/512/3625/3625253.png'); }

.carousel-dark .carousel-indicators [data-bs-target] {
    background-color: #fff; }
.carousel-indicators {
    position: absolute;
    z-index: 20; }

.nav-item {
    margin-left: 4%; }

.forButton {
    display: flex;
    justify-content: center;
    margin-bottom: 10%; }

.btn-close {
    position: relative;
    bottom: 28px;
    left: 90%; }
